@charset "UTF-8";
@font-face {
  font-family: "PT Mono";
  src: url("../fonts/PT-Mono.woff2") format("woff2"), url("../fonts/PT-Mono.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/Roboto-Condensed.woff2") format("woff2"), url("../fonts/Roboto-Condensed.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/Roboto-Condensed-Bold.woff2") format("woff2"), url("../fonts/Roboto-Condensed-Bold.woff2") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  scroll-behavior: smooth;
  height: 100%;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #f2f5f6;
}

body {
  width: 100%;
  height: 100%;
  background-color: #041827;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

button {
  padding: 0;
  text-transform: uppercase;
  border-radius: 3px;
  border: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  cursor: pointer;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.header {
  position: fixed;
  top: 0;
  z-index: 1;
  width: 100%;
  min-height: 120px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}
@media (max-width: 1219px) {
  .header {
    position: absolute;
    min-height: 100px;
  }
}
@media (max-width: 769px) {
  .header {
    min-height: 70px;
  }
}

.header-container {
  box-sizing: border-box;
  display: flex;
  width: 1160px;
  margin: auto;
}
@media (max-width: 1219px) {
  .header-container {
    width: 770px;
    padding: 20px 70px 0 50px;
    justify-content: space-between;
  }
}
@media (max-width: 769px) {
  .header-container {
    width: 320px;
    padding: 10px 35px 0 26px;
  }
}

.header-logo {
  display: flex;
  margin-right: 160px;
  text-decoration: none;
}
@media (max-width: 1219px) {
  .header-logo {
    margin: 0;
  }
}

.header-logo__icon {
  margin-top: 30px;
  margin-right: 15px;
}
@media (max-width: 1219px) {
  .header-logo__icon {
    margin-top: 0;
  }
}
@media (max-width: 769px) {
  .header-logo__icon {
    width: 35px;
    height: 35px;
    margin: 5px 0 0;
  }
}

.header-logo__title-container {
  display: flex;
  flex-direction: column;
}

.header-logo__title {
  min-width: 145px;
  height: 25px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 25px;
  margin: 35px 0 5px;
  color: #f2f5f6;
}
@media (max-width: 1219px) {
  .header-logo__title {
    margin-top: 5px;
  }
}
@media (max-width: 769px) {
  .header-logo__title {
    display: none;
  }
}

.header-logo__subtitle {
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 500;
  font-size: 12px;
  color: rgba(255, 255, 255, 0.7);
}
@media (max-width: 769px) {
  .header-logo__subtitle {
    display: none;
  }
}

.nav {
  display: flex;
}

.nav-list {
  display: grid;
  gap: 30px;
  margin: 45px 145px 0 0;
  padding: 0;
  list-style-type: none;
  grid-template-columns: repeat(2, 225px);
}
@media (max-width: 1219px) {
  .nav-list {
    gap: 20px;
    margin: 0;
    grid-template-columns: repeat(2, 40px);
  }
}

.nav-list__item-link {
  display: flex;
  font-family: "PT Mono", monospace;
  text-decoration: none;
  color: #f2f5f6;
  font-size: 16px;
}

@media (max-width: 1219px) {
  .nav-list__item-text {
    display: none;
  }
}

.nav-list__icon {
  margin-right: 15px;
}
@media (max-width: 1219px) {
  .nav-list__icon {
    margin: 0;
    padding: 10px;
  }
}

.nav-button {
  width: 160px;
  height: 40px;
  padding: 10px 15px;
  margin-top: 35px;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  font-weight: 700;
  background-color: transparent;
  text-transform: uppercase;
  color: #f2f5f6;
  box-shadow: 0 0 0 1px #f2f5f6 inset;
}
@media (max-width: 1219px) {
  .nav-button {
    display: none;
  }
}

.main {
  position: relative;
  min-height: 880px;
  background-image: url("../img/main-background.jpg");
  background-position: center;
}
.main::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%);
}
@media (max-width: 1219px) {
  .main {
    min-height: 640px;
    background-image: url("../img/main-background-tablet.jpg");
    background-position-x: -125px;
  }
}
@media (max-width: 769px) {
  .main {
    min-height: 560px;
    background-image: url("../img/main-background-mobile.jpg");
    background-position-x: -225px;
  }
}

.main-container {
  position: relative;
  z-index: 0;
  box-sizing: border-box;
  width: 1220px;
  margin: auto;
  padding: 305px 125px 280px;
}
@media (max-width: 1219px) {
  .main-container {
    width: 770px;
    padding: 180px 50px 205px;
  }
}
@media (max-width: 769px) {
  .main-container {
    width: 320px;
    padding: 150px 25px;
  }
}

.main__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 80px;
  line-height: 90px;
  font-weight: 700;
  letter-spacing: 0.07em;
  color: #f2f5f6;
  margin: 0 0 40px;
  text-transform: uppercase;
}
@media (max-width: 1219px) {
  .main__title {
    height: 60px;
    line-height: 75px;
    font-size: 64px;
    margin-bottom: 20px;
  }
}
@media (max-width: 769px) {
  .main__title {
    font-size: 36px;
    line-height: 42px;
    height: 75px;
  }
}

.main__subtitle {
  max-width: 685px;
  min-height: 70px;
  font-family: "PT Mono", monospace;
  font-weight: 400;
  line-height: 34px;
  font-size: 22px;
  color: #f2f5f6;
  margin: 0 0 40px;
}
@media (max-width: 1219px) {
  .main__subtitle {
    max-width: 670px;
    margin-bottom: 50px;
  }
}
@media (max-width: 769px) {
  .main__subtitle {
    font-size: 14px;
    line-height: 22px;
    min-height: 35px;
  }
}

.main__link {
  position: relative;
  display: block;
  box-sizing: border-box;
  text-align: center;
  text-transform: uppercase;
  border-radius: 3px;
  background-color: #00b2ff;
  width: 410px;
  height: 55px;
  padding: 15px 25px;
}
.main__link::after {
  content: "Получить бесплатную консультацию";
  position: absolute;
  display: block;
  top: 16px;
  left: 25px;
  width: 360px;
  min-height: 20px;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.05em;
  color: #f2f5f6;
}
@media (max-width: 1219px) {
  .main__link {
    margin-left: 45px;
  }
}
@media (max-width: 769px) {
  .main__link {
    width: 270px;
    height: 40px;
    padding: 10px 30px;
    margin: 0;
    font-size: 14px;
    line-height: 16px;
  }
  .main__link::after {
    content: "бесплатная консультация";
    font-size: 14px;
    line-height: 16px;
    top: 12px;
    left: 30px;
    width: 210px;
  }
}

.advantages-list {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  margin: 0;
  padding: 120px 0 120px;
  background-color: rgb(248, 253, 255);
}
@media (max-width: 1219px) {
  .advantages-list {
    display: grid;
    grid-template-columns: repeat(2, 320px);
    padding: 80px 50px 80px;
  }
}
@media (max-width: 769px) {
  .advantages-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 25px;
  }
}

.advantages-list__item {
  position: relative;
  box-sizing: border-box;
  width: 595px;
  min-height: 300px;
  padding: 94px 44px 34px 45px;
  border: 1px solid #ebebeb;
  border-left: none;
  box-shadow: inset 5px 0 #00b2ff;
}
.advantages-list__item--primary {
  background-color: #00b2ff;
  border-color: #00b2ff;
}
@media (max-width: 1219px) {
  .advantages-list__item {
    width: 320px;
    min-height: 360px;
    padding: 94px 29px 34px 45px;
  }
}
@media (max-width: 769px) {
  .advantages-list__item {
    width: 270px;
    min-height: 340px;
    padding: 79px 29px 34px 30px;
  }
}

.advantages-list__item-icon {
  position: absolute;
  top: 0;
  left: 0;
}
.advantages-list__item-icon--stopwatch {
  top: 39px;
  left: 40px;
}
.advantages-list__item-icon--hired {
  top: 39px;
  left: 41px;
}
.advantages-list__item-icon--detective {
  top: 39px;
  left: 35px;
}
.advantages-list__item-icon--tag {
  top: 39px;
  left: 40px;
}
@media (max-width: 769px) {
  .advantages-list__item-icon--stopwatch, .advantages-list__item-icon--hired, .advantages-list__item-icon--detective, .advantages-list__item-icon--tag {
    top: 30px;
    left: 30px;
    width: 30px;
    height: 30px;
  }
}

.advantages-list__item-title {
  color: #00b2ff;
  margin: 0 0 15px;
  font-size: 24px;
  font-weight: 400;
  line-height: 35px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
}
@media (max-width: 1219px) {
  .advantages-list__item-title {
    line-height: 30px;
  }
}
@media (max-width: 769px) {
  .advantages-list__item-title {
    font-size: 20px;
    line-height: 28px;
  }
}

.advantages-list__item--primary .advantages-list__item-title {
  color: #f2f5f6;
}

.advantages-list__item-description {
  font-family: "PT Mono", monospace;
  font-size: 16px;
  line-height: 21px;
  color: #041827;
  margin: 0;
}
@media (max-width: 1219px) {
  .advantages-list__item-description {
    width: 245px;
  }
}
@media (max-width: 769px) {
  .advantages-list__item-description {
    width: 210px;
    font-size: 14px;
    line-height: 19px;
  }
}

.advantages-list__item--primary .advantages-list__item-description {
  color: #f2f5f6;
}

.about {
  background-color: rgb(248, 253, 255);
  padding-bottom: 100px;
}
@media (max-width: 1219px) {
  .about {
    padding-bottom: 80px;
  }
}

.about-container {
  box-sizing: border-box;
  padding: 5px 0 20px 250px;
  min-height: 320px;
  width: 1220px;
  margin: 0 auto;
  background-image: url("../img/2022.png");
  background-repeat: no-repeat;
  background-position: left;
}
@media (max-width: 1219px) {
  .about-container {
    width: 670px;
    min-height: 380px;
    padding: 0 0 0 175px;
    background-position: top left;
  }
}
@media (max-width: 769px) {
  .about-container {
    width: 320px;
    min-height: 305px;
    padding: 0 25px 0;
    background: none;
  }
}

.about-title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #041827;
  font-size: 36px;
  line-height: 120%;
  height: 45px;
  font-weight: 400;
  margin: 0 0 15px;
}
@media (max-width: 769px) {
  .about-title {
    font-size: 28px;
    line-height: 39px;
    height: 35px;
  }
}

.about-description-container {
  min-height: 170px;
  margin-bottom: 20px;
}

.about-description {
  font-family: "PT Mono", monospace;
  font-size: 16px;
  line-height: 120%;
  color: #041827;
  margin: 0 0 20px;
}
@media (max-width: 769px) {
  .about-description {
    font-size: 14px;
    line-height: 19px;
    margin-bottom: 10px;
  }
}

@media (max-width: 769px) {
  .about-description__mobile {
    display: none;
  }
}

.about-button {
  width: 180px;
  height: 45px;
  padding: 14px 12px;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #00b2ff;
  background-color: rgb(248, 253, 255);
  border: 1px solid #00b2ff;
}
@media (max-width: 769px) {
  .about-button {
    width: 160px;
    height: 35px;
    padding: 8px 15px;
  }
}

.products-container {
  box-sizing: border-box;
  width: 1220px;
  margin: 0 auto;
  padding: 70px 0 140px;
}
@media (max-width: 1219px) {
  .products-container {
    width: 770px;
    padding: 60px 50px 75px;
  }
}
@media (max-width: 769px) {
  .products-container {
    width: 320px;
    padding: 50px 25px 86px;
  }
}

.products__title {
  margin: 0 0 50px;
  font-size: 36px;
  line-height: 50px;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
}
@media (max-width: 1219px) {
  .products__title {
    height: 85px;
  }
}
@media (max-width: 769px) {
  .products__title {
    display: none;
  }
}

.products__title-mobile {
  display: none;
}
@media (max-width: 769px) {
  .products__title-mobile {
    color: #ffffff;
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-weight: 400;
    display: block;
    font-size: 28px;
    line-height: 39px;
    margin: 0 0 40px;
    height: 64px;
  }
}

.products-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 30px 85px;
}
@media (max-width: 1219px) {
  .products-list {
    flex-direction: column;
    align-items: center;
  }
}

.products-list__item {
  position: relative;
  box-sizing: border-box;
  width: 350px;
  min-height: 650px;
  padding: 50px 80px 0 40px;
}
.products-list__item--first {
  background-image: url("../img/products/product-desktop-1.jpg");
}
.products-list__item--second {
  background-image: url("../img/products/product-desktop-2.jpg");
}
.products-list__item--third {
  background-image: url("../img/products/product-desktop-3.jpg");
}
@media (max-width: 1219px) {
  .products-list__item {
    width: 670px;
    min-height: 250px;
    padding: 40px 45px 0;
  }
  .products-list__item--first {
    background-image: url("../img/products/product-tablet-1.jpg");
  }
  .products-list__item--second {
    background-image: url("../img/products/product-tablet-2.jpg");
  }
  .products-list__item--third {
    background-image: url("../img/products/product-tablet-3.jpg");
  }
}
@media (max-width: 769px) {
  .products-list__item {
    padding: 30px 30px 0;
    width: 270px;
  }
  .products-list__item--first {
    background-image: url("../img/products/product-mobile-1.jpg");
  }
  .products-list__item--second {
    background-image: url("../img/products/product-mobile-2.jpg");
  }
  .products-list__item--third {
    background-image: url("../img/products/product-mobile-3.jpg");
  }
}

.products-list__item-title {
  width: 230px;
  height: 55px;
  margin: 0;
  font-size: 20px;
  color: #ffffff;
  font-weight: 400;
  line-height: 30px;
}
@media (max-width: 769px) {
  .products-list__item-title {
    width: 150px;
    font-size: 18px;
    height: 50px;
  }
}

.products-list__item-button {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0;
  right: 0;
  width: 60px;
  height: 60px;
  background-color: #00b2ff;
  border-radius: 3px 0 0 0;
}
@media (max-width: 769px) {
  .products-list__item-button {
    width: 40px;
    height: 40px;
  }
}

@media (max-width: 769px) {
  .products-list__item-icon {
    width: 6px;
    height: 6px;
  }
}

.feedback {
  box-sizing: border-box;
  padding: 170px 0;
  min-height: 840px;
  background: linear-gradient(139deg, #026693 0, #026693 33.5%, #00b2ff 33.6%);
}
@media (max-width: 1219px) {
  .feedback {
    padding: 50px 0;
    min-height: 580px;
    background: #00b2ff;
  }
}
@media (max-width: 769px) {
  .feedback {
    padding: 50px 25px;
    min-height: 750px;
  }
}

.feedback__picture {
  margin-top: 76px;
  margin-right: 35px;
  max-width: 510px;
  min-width: 400px;
  height: 100%;
}
@media (max-width: 1219px) {
  .feedback__picture {
    display: none;
  }
}

.feedback-container {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  max-width: 1210px;
  min-width: 1024px;
}
@media (max-width: 1219px) {
  .feedback-container {
    min-width: 768px;
    justify-content: center;
  }
}
@media (max-width: 769px) {
  .feedback-container {
    min-width: 270px;
  }
}

.feedback__content-container {
  width: 585px;
  color: #ffffff;
}
@media (max-width: 1219px) {
  .feedback__content-container {
    width: 670px;
  }
}
@media (max-width: 769px) {
  .feedback__content-container {
    width: auto;
    min-width: 270px;
  }
}

.feedback__content-container h2 {
  font-size: 36px;
  line-height: 45px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  margin: 0 0 15px;
}
@media (max-width: 1219px) {
  .feedback__content-container h2 {
    margin-bottom: 10px;
  }
}
@media (max-width: 769px) {
  .feedback__content-container h2 {
    font-size: 30px;
    line-height: 35px;
    margin-bottom: 20px;
  }
}

.feedback__content-container p {
  font-size: 16px;
  line-height: 20px;
  margin: 0 0 35px 5px;
  font-family: "PT Mono", monospace;
}
@media (max-width: 1219px) {
  .feedback__content-container p {
    margin: 0 0 25px;
  }
}
@media (max-width: 769px) {
  .feedback__content-container p {
    font-size: 14px;
    line-height: 15px;
    margin: 0 0 40px;
  }
}

.form {
  display: flex;
  flex-wrap: wrap;
  gap: 20px 30px;
  padding-left: 5px;
  color: #ffffff;
  background-color: #00b2ff;
}
@media (max-width: 1219px) {
  .form {
    padding: 0;
  }
}
@media (max-width: 769px) {
  .form {
    display: block;
  }
}

.form__field input,
.form__textarea textarea {
  font-family: "PT Mono", monospace;
  font-size: 16px;
  box-sizing: border-box;
  display: block;
  border-radius: 3px;
  border: 0;
  background-color: rgba(4, 20, 39, 0.15);
  color: #ffffff;
  outline: none;
}
.form__field input::placeholder,
.form__textarea textarea::placeholder {
  color: #ffffff;
  opacity: 1;
}

.form__field input {
  width: 275px;
  height: 50px;
  padding: 15px;
}
@media (max-width: 1219px) {
  .form__field input {
    width: 320px;
  }
}
@media (max-width: 769px) {
  .form__field input {
    width: 100%;
    margin-bottom: 20px;
  }
}

.form__textarea textarea {
  width: 580px;
  height: 155px;
  resize: none;
  padding: 25px 15px;
  margin: 0 0 15px;
}
@media (max-width: 1219px) {
  .form__textarea textarea {
    width: 670px;
  }
}
